/****************************************************************************************/
/***************************** c9 core block edits for client custom theme
/****************************************************************************************/
:root {
  font-family: 'helvetica neue', helvetica, sans-serif; }

:root {
  font-family: "helvetica neue", "helvetica", times, serif; }

.editor-styles-wrapper,
.wp-block-pullquote blockquote > .block-library-pullquote__content .editor-rich-text__tinymce[data-is-empty="true"]::before,
.wp-block-pullquote blockquote > .editor-rich-text p {
  font-size: 2rem; }

.editor-styles-wrapper div,
.editor-post-title__block .editor-post-title__input {
  font-family: "helvetica neue", "helvetica", times, serif !important; }

.editor-styles-wrapper ul li,
.editor-styles-wrapper ol li {
  font-size: 2rem;
  color: #4a4a4a; }

.editor-styles-wrapper .h1,
.editor-styles-wrapper .h2,
.editor-styles-wrapper .h3,
.editor-styles-wrapper .h4,
.editor-styles-wrapper .h5,
.editor-styles-wrapper .h6,
.editor-styles-wrapper h1,
.editor-styles-wrapper h2,
.editor-styles-wrapper h3,
.editor-styles-wrapper h4,
.editor-styles-wrapper h5,
.editor-styles-wrapper h6 {
  font-family: "helvetica neue", "helvetica", times, serif;
  font-weight: 600; }

